@font-face {
  font-family: 'directions';
  src:  url('https://media.busyrooms.co/fonts/icomoon/directions.eot?njtrg4');
  src:  url('https://media.busyrooms.co/fonts/icomoon/directions.eot?njtrg4#iefix') format('embedded-opentype'),
    url('https://media.busyrooms.co/fonts/icomoon/directions.ttf?njtrg4') format('truetype'),
    url('https://media.busyrooms.co/fonts/icomoon/directions.woff?njtrg4') format('woff'),
    url('https://media.busyrooms.co/fonts/icomoon/directions.svg?njtrg4#directions') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-directions-"], [class*=" icon-directions-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'directions' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

