@font-face {
  font-family: 'payment';
  src:  url('https://media.busyrooms.co/fonts/icomoon/payment.eot?v8a583');
  src:  url('https://media.busyrooms.co/fonts/icomoon/payment.eot?v8a583#iefix') format('embedded-opentype'),
    url('https://media.busyrooms.co/fonts/icomoon/payment.ttf?v8a583') format('truetype'),
    url('https://media.busyrooms.co/fonts/icomoon/payment.woff?v8a583') format('woff'),
    url('https://media.busyrooms.co/fonts/icomoon/payment.svg?v8a583#payment') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-payment-"], [class*=" icon-payment-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'payment' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

